import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams,ViewController,ModalController} from 'ionic-angular';

/**
 * Generated class for the Modal1Page page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */
@IonicPage()
@Component({
  selector: 'page-modal1',
  templateUrl: 'modal1.html',
})

export class Modal1Page {

	items :String[];

	temp:any;//optional

	headr:any

	//private myModal:ModalController

  constructor(public navCtrl: NavController, public navParams: NavParams, private view:ViewController,private myModal:ModalController) {
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad Modal1Page');
    const getData=this.navParams.get('name');
    console.log(getData);
    this.headr=getData.vehicleType;
    this.items=getData.brands;
    this.temp=getData.brands;//tried to fix  toLowerCase() order prblm

  }

  closeModal(){
  	this.view.dismiss();
  }

  initializeItems() {

    this.items = this.temp;

  }

  getItems(ev: any) {
    // Reset items back to all of the items
    this.initializeItems();

    // set val to the value of the searchbar
    const val = ev.target.value;

    // if the value is an empty string don't filter the items
    if (val && val.trim() != '') {

      this.items = this.items.filter((item) => {
        return (item.toLowerCase().indexOf(val.toLowerCase()) > -1);
      })
      console.log(this.items);


   	 }
	}


 modal2(name:String) {


    	const modal=this.myModal.create("Modal2Page",{name});

  		modal.present();
    }

}



